footer {
  display: flex;
  margin-top: 47px;
  margin-bottom: 57px; }
  footer a {
    text-decoration: none; }
  footer .with-love {
    margin-top: 0;
    font-size: 19px; }
    @media (max-width: 424px) {
      footer .with-love {
        margin-top: 25px; }
        footer .with-love .from {
          display: none; } }
    footer .with-love .icon-love {
