    font-size: 14px;
    align-self: flex-end; }
    @media (max-width: 424px) {
      footer .fork-and-fix {
        display: none; } }

nav {
  display: flex;
  margin-top: 60px;
  margin-left: 80px;
  margin-right: 80px; }
  @media (max-width: 424px) {
    nav {
      margin-top: 25px;
      margin-left: 25px;
      margin-right: 25px; } }
  @media (min-width: 1280px) {
    nav {
      width: 1280px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 30px;
      padding-right: 30px; } }
  nav > a {
    align-self: center;
    color: #747474;
    font-size: 16px;
    line-height: 26px;
    margin-right: 34px;
    text-decoration: none;
    white-space: nowrap;
    transition: color 100ms ease-in-out; }
    @media (max-width: 424px) {
      nav > a {
        margin-right: 10px; } }
    nav > a:hover, nav > a.-active {
      color: #fff; }
    nav > a .icon-github path {
      transition: fill 100ms ease-in-out; }
    nav > a:hover .icon-github path {
      fill: #fff; }
  nav .share {
    box-sizing: border-box;
    color: #fff;
    padding: 9px 34px 11px;
    border: 1px solid #313131;
    transition: border-color 100ms ease-in-out;
    margin-right: 8px; }
    nav .share:hover {
      border-color: #fff; }
    nav .share.-right {
      margin-left: auto; }
    @media (max-width: 640px) {
      nav .share {
        display: none; } }
    @media (max-width: 424px) {
      nav .share {
        display: none; } }
    @media (min-width: 425px) and (max-width: 1023px) {
      nav .share {
        padding: 5px 11px; } }
  nav .icon-github,
  nav .icon-facebook,
  nav .icon-twitter {
